___CSS_LOADER_EXPORT___.push([module.id, `.privacy-policy-container {
  max-width: 900px;
  margin: 2rem auto;
  padding: 1rem;
  font-family: "Helvetica", sans-serif;
  color: #333;
  line-height: 1.6;
}

.privacy-policy-content h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
  text-align: center;
}

.privacy-policy-content h2 {
  font-size: 1.6rem;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.privacy-policy-content h3 {
  font-size: 1.2rem;
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
}

.privacy-policy-content p {
  font-size: 1rem;
  margin-bottom: 1rem;
  text-align: justify;
}

.privacy-policy-content ul {
  margin-left: 1.5rem;
  margin-bottom: 1rem;
}

.privacy-policy-content li {
  margin-bottom: 0.5rem;
}

.last-updated {
  font-style: italic;
  font-size: 0.9rem;
  text-align: center;
  margin-bottom: 1.5rem;
}

a {
  color: #007bff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}
